import React from 'react'
import "./Skills.css"
import { skillsData } from '../../data';
import {motion} from "framer-motion"


const fadeInAnimationVariants = {
  initial: {
    opacity: 0,
    y: 0
  },
  animate: index => ({
    opacity: 1,
    y: 0,
    transition: {
      delay: 0.05 * index
    }
  })
}

const Skills = () => {
  return (
    <div className='skills'>
          <section
      id="skills"
      className="mb-28 max-w-[53rem] scroll-mt-28 text-center sm:mb-40"
    >
      <h1 className='font-medium capitalize mb-8 text-center sk'>My skills</h1>
      <ul className="flex flex-wrap justify-center gap-2 text-lg text-gray-800 h-full w-full ">
        {skillsData.map((skill, index) => (
          <motion.li
            className="bg-white borderBlack rounded-xl px-5 py-3"
            key={index}
            variants={fadeInAnimationVariants}
            initial="initial"
            whileInView="animate"

            custom={index}
          >
            {skill}
          </motion.li>
        ))}
      </ul>
    </section>


    </div>
  );
};

export default Skills;
